'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { ArrowLeft, Save, Car, Ambulance, Helicopter } from 'lucide-react';
import Link from 'next/link';

export default function NewVehiclePage() {
  const router = useRouter();
  const [formData, setFormData] = useState({
    name: '',
    type: 'car' as 'car' | 'ambulance' | 'helicopter',
    model: '',
    licensePlate: '',
    registrationNumber: '',
    status: 'active' as 'active' | 'maintenance' | 'out_of_service',
    location: '',
    mileage: '',
    flightHours: '',
    notes: ''
  });

  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      // In a real app, this would call an API endpoint
      // For now, we'll simulate a delay and redirect
      await new Promise(resolve => setTimeout(resolve, 1000));

      console.log('New vehicle data:', formData);
      alert('Vehicle created successfully! (This is a demo - in production, this would save to database)');
      router.push('/vehicles');
    } catch (error) {
      console.error('Error creating vehicle:', error);
      alert('Failed to create vehicle');
    } finally {
      setIsSubmitting(false);
    }
  };

  const getVehicleIcon = () => {
    switch (formData.type) {
      case 'car': return <Car className="h-6 w-6" />;
      case 'ambulance': return <Ambulance className="h-6 w-6" />;
      case 'helicopter': return <Helicopter className="h-6 w-6" />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <main className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <Link
            href="/vehicles"
            className="inline-flex items-center gap-2 text-gray-600 hover:text-gray-900 transition-colors mb-4"
          >
            <ArrowLeft className="h-5 w-5" />
            Return to Fleet Management
          </Link>
          <h1 className="text-3xl font-bold text-gray-900">Add New Vehicle</h1>
          <p className="mt-1 text-sm text-gray-600">
            Enter vehicle information to add to the fleet management system
          </p>
        </div>

        <form onSubmit={handleSubmit}>
          {/* Basic Information */}
          <Card className="mb-6">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                {getVehicleIcon()}
                Basic Vehicle Information
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-1">
                    Vehicle Name <span className="text-red-600">*</span>
                  </label>
                  <input
                    type="text"
                    id="name"
                    required
                    value={formData.name}
                    onChange={(e) => handleChange('name', e.target.value)}
                    placeholder="e.g., Emergency Response Vehicle Alpha"
                    className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  />
                </div>

                <div>
                  <label htmlFor="type" className="block text-sm font-medium text-gray-700 mb-1">
                    Vehicle Type <span className="text-red-600">*</span>
                  </label>
                  <select
                    id="type"
                    required
                    value={formData.type}
                    onChange={(e) => handleChange('type', e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  >
                    <option value="car">Emergency Response Car (Toyota Prado Charlie)</option>
                    <option value="ambulance">Ambulance (Ford Alpha/Bravo)</option>
                    <option value="helicopter">Medical Helicopter</option>
                  </select>
                </div>

                <div>
                  <label htmlFor="model" className="block text-sm font-medium text-gray-700 mb-1">
                    Vehicle Model <span className="text-red-600">*</span>
                  </label>
                  <input
                    type="text"
                    id="model"
                    required
                    value={formData.model}
                    onChange={(e) => handleChange('model', e.target.value)}
                    placeholder={formData.type === 'car' ? 'Toyota Prado' : formData.type === 'ambulance' ? 'Ford Transit' : 'AS350'}
                    className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  />
                </div>

                <div>
                  <label htmlFor="status" className="block text-sm font-medium text-gray-700 mb-1">
                    Current Status <span className="text-red-600">*</span>
                  </label>
                  <select
                    id="status"
                    required
                    value={formData.status}
                    onChange={(e) => handleChange('status', e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  >
                    <option value="active">Active - In Service</option>
                    <option value="maintenance">Under Maintenance</option>
                    <option value="out_of_service">Out of Service</option>
                  </select>
                </div>

                <div>
                  <label htmlFor="licensePlate" className="block text-sm font-medium text-gray-700 mb-1">
                    License Plate Number <span className="text-red-600">*</span>
                  </label>
                  <input
                    type="text"
                    id="licensePlate"
                    required
                    value={formData.licensePlate}
                    onChange={(e) => handleChange('licensePlate', e.target.value)}
                    placeholder="e.g., HMC-123"
                    className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  />
                </div>

                <div>
                  <label htmlFor="registrationNumber" className="block text-sm font-medium text-gray-700 mb-1">
                    Registration Number <span className="text-red-600">*</span>
                  </label>
                  <input
                    type="text"
                    id="registrationNumber"
                    required
                    value={formData.registrationNumber}
                    onChange={(e) => handleChange('registrationNumber', e.target.value)}
                    placeholder="e.g., REG-2026-001"
                    className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  />
                </div>

                <div>
                  <label htmlFor="location" className="block text-sm font-medium text-gray-700 mb-1">
                    Current Location <span className="text-red-600">*</span>
                  </label>
                  <input
                    type="text"
                    id="location"
                    required
                    value={formData.location}
                    onChange={(e) => handleChange('location', e.target.value)}
                    placeholder="e.g., Main Hospital Parking"
                    className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  />
                </div>

                {formData.type !== 'helicopter' && (
                  <div>
                    <label htmlFor="mileage" className="block text-sm font-medium text-gray-700 mb-1">
                      Current Mileage (km)
                    </label>
                    <input
                      type="number"
                      id="mileage"
                      value={formData.mileage}
                      onChange={(e) => handleChange('mileage', e.target.value)}
                      placeholder="e.g., 50000"
                      className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                    />
                  </div>
                )}

                {formData.type === 'helicopter' && (
                  <div>
                    <label htmlFor="flightHours" className="block text-sm font-medium text-gray-700 mb-1">
                      Flight Hours
                    </label>
                    <input
                      type="number"
                      id="flightHours"
                      value={formData.flightHours}
                      onChange={(e) => handleChange('flightHours', e.target.value)}
                      placeholder="e.g., 2500"
                      className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                    />
                  </div>
                )}
              </div>

              <div className="mt-6">
                <label htmlFor="notes" className="block text-sm font-medium text-gray-700 mb-1">
                  Additional Notes
                </label>
                <textarea
                  id="notes"
                  rows={4}
                  value={formData.notes}
                  onChange={(e) => handleChange('notes', e.target.value)}
                  placeholder="Enter any additional information about this vehicle..."
                  className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                />
              </div>
            </CardContent>
          </Card>

          {/* Form Actions */}
          <div className="flex items-center justify-end gap-4">
            <Link href="/vehicles">
              <Button type="button" variant="outline">
                Cancel
              </Button>
            </Link>
            <Button type="submit" disabled={isSubmitting} className="min-w-[150px]">
              <Save className="h-4 w-4 mr-2" />
              {isSubmitting ? 'Creating Vehicle...' : 'Create Vehicle'}
            </Button>
          </div>
        </form>

        <div className="mt-8 p-4 bg-blue-50 border border-blue-200 rounded-md">
          <h3 className="text-sm font-semibold text-blue-900 mb-2">Note</h3>
          <p className="text-sm text-blue-800">
            After creating the vehicle, you&apos;ll need to add equipment to it from the Equipment Management section.
            Each vehicle should have all required equipment registered for proper maintenance tracking.
          </p>
        </div>
      </main>
    </div>
  );
}